
import Vue, { PropType } from "vue";
import orderBy from "lodash.orderby";

import { unlockBody, lockBody } from "@/lib/body-lock";

import {
  $filters, $filterItems, $isFiltersPending, $isProductsPending, $products,
  $selectedFilters, $productsTotal,
  LIMIT,
} from "../model";

import { Category } from "@/services/categories.service";
import { Options } from "@/services/products.service";

import { BrButton, BrLoader } from "@/shared";
import Filters from "./Filters.vue";
import ProductCard from "./ProductCard.vue";
import ProductsHeader from "./ProductsHeader.vue";
import Pagination from "./Pagination.vue";

export default Vue.extend({
  name: "ProductsContainer",

  effector() {
    return {
      $filters,
      $filterItems,
      $selectedFilters,

      $isFiltersPending,
      $isProductsPending,

      $products,
      $productsTotal,
    };
  },

  components: {
    BrButton,
    BrLoader,
    Filters,

    ProductCard,
    ProductsHeader,
    Pagination,
  },

  props: {
    currentCategory: {
      type: Object as PropType<Category>,
      required: true,
    },
  },

  data: () => ({
    isOpened: false,
    $filters: {} as State<typeof $filters>,
    $selectedFilters: {} as State<typeof $selectedFilters>,
    $productsTotal: {} as State<typeof $productsTotal>,
    $products: {} as State<typeof $products>,
    $isFiltersPending: {} as State<typeof $isFiltersPending>,
  }),

  computed: {
    limit(): number {
      return LIMIT;
    },
    filters() {
      return this.$filters[this.currentCategory.id];
    },
    stats(): { offset: number; sortBy: string; from: number; to: number } {
      const { offset, sortBy } = this.$selectedFilters.metadata;

      return {
        offset,
        sortBy,
        from: offset + 1,
        to: this.$productsTotal < offset + this.limit ? this.$productsTotal : offset + this.limit,
      };
    },
    products() {
      return orderBy(
        this.$products,
        [this.$products, "price"],
        this.$selectedFilters.metadata.sortBy !== "desc" ? ["desc", "asc"] : ["asc", "desc"],
      );
    },
    withFilters(): boolean {
      return !this.$isFiltersPending && (this.filters && !!Object.keys(this.filters).length);
    },
  },

  methods: {
    handleToggle() {
      this.isOpened = !this.isOpened;

      // eslint-disable-next-line no-unused-expressions
      this.isOpened ? lockBody() : unlockBody();
    },
    handleOffset(offset: number) {
      const query = {
        ...this.$selectedFilters.metadata,
        offset,
      } as { [key: string]: any };

      this.$router.push({
        name: "ProductsPage",
        params: { category: this.currentCategory.path },
        query,
      });
    },
    handleSort(sortBy: "asc" | "desc") {
      const query = {
        ...this.$selectedFilters.metadata,
        sortBy,
      } as { [key: string]: any };

      this.$router.push({
        name: "ProductsPage",
        params: { category: this.currentCategory.path },
        query,
      });
    },
    handleUpdateRoute(queries: Options) {
      const query = {
        ...queries.metadata,
        filters: queries.filters,
      } as { [key: string]: any };

      this.$router.push({
        name: "ProductsPage",
        params: { category: this.currentCategory.path },
        query,
      });
    },
  },
});
