
import Vue from "vue";

export default Vue.extend({
  name: "BrUserControl",

  components: {
    cartIcon: () => import("@/assets/vector/cart.svg"),
    menuIcon: () => import("@/assets/vector/menu.svg"),
  },

  inheritAttrs: false,

  props: {
    icon: {
      type: String,
      default: "",
    },
    customTag: {
      type: String,
      default: null,
    },
  },

  computed: {
    tag(): string {
      return this.customTag || (this.$attrs.to ? "router-link" : "button");
    },
  },
});
