
import Vue from "vue";

import Dispatch from "./dispatch";
import plusIcon from "@/assets/vector/plus.svg";

export default Dispatch.extend({
  name: "BrCollapseItem",

  components: {
    plusIcon,
  },

  props: {
    name: {
      type: [String, Number],
      required: true,
    },
    container: {
      type: Boolean,
      default: true,
    },
  },

  data: () => ({
    parent: null as null | Vue,
  }),

  computed: {
    isActive(): boolean {
      if (!this.parent) {
        return false;
      }

      const { activeNames } = this.parent.$data;

      return activeNames.indexOf(this.name) > -1;
    },
  },

  created() {
    let parent = this.$parent;

    while (parent) {
      if (parent.$options.name !== "BrCollapse") {
        parent = parent.$parent;
      } else {
        this.parent = parent;
        break;
      }
    }
  },

  mounted() {
    const body = this.$refs.body as HTMLElement;

    body.style.height = `${body.scrollHeight}px`;
  },

  methods: {
    handleClick() {
      this.dispatch({ componentName: "BrCollapse", eventName: "item-click", params: [this.name] });
    },
    beforeEnter(el: HTMLElement) {
      el.style.height = "0";
    },
    enter(el: HTMLElement) {
      el.style.height = `${el.scrollHeight}px`;
    },
    beforeLeave(el: HTMLElement) {
      el.style.height = `${el.scrollHeight}px`;
    },
    leave(el: HTMLElement) {
      el.style.height = "0";
    },
  },
});
