
import Vue from "vue";
import minIcon from "@/assets/vector/min.svg";
import maxIcon from "@/assets/vector/plus.svg";

export default Vue.extend({
  name: "BrInputNumber",

  components: {
    minIcon,
    maxIcon,
  },

  props: {
    value: {
      type: Number,
      default: 0,
    },
    min: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 9999,
    },
  },

  methods: {
    increase() {
      if (this.value < this.max) {
        const value = this.value + 1;
        this.$emit("input", value);
      }
    },
    decrease() {
      if (this.value > this.min) {
        const value = this.value - 1;
        this.$emit("input", value);
      }
    },
  },
});
