
import Vue from "vue";
import Dispatch from "./dispatch";

export default Dispatch.extend({
  name: "BrRadio",

  inheritAttrs: false,

  model: {
    prop: "checked",
    event: "change",
  },

  props: {
    checked: {
      type: [String, Boolean, Number],
      default: "",
    },
    value: {
      type: [String, Boolean, Number],
      default: "",
    },
  },

  data: () => ({
    radioGroup: null as Vue | null,
  }),

  computed: {
    isChecked(): boolean {
      const value = !this.radioGroup ? this.checked : this.$parent.$props.checked;
      return value === this.value;
    },
    name(): string {
      return !this.radioGroup ? this.$attrs.name : this.$parent.$props.groupName;
    },
  },

  created() {
    let parent = this.$parent;

    while (parent) {
      if (parent.$options.name !== "BrRadioGroup") {
        parent = parent.$parent;
      } else {
        this.radioGroup = parent;
        break;
      }
    }
  },

  methods: {
    handleChange() {
      if (!this.radioGroup) {
        this.$emit("change", this.value);
      } else {
        this.dispatch({
          componentName: "BrRadioGroup",
          eventName: "change",
          params: [this.value],
        });
      }
    },
  },
});
